const Resume = React.lazy(() => import("../Resume/Resume"))
const ContactMe = React.lazy(() => import("../ContactMe/ContactMe"))
const Footer = React.lazy(() => import("../Footer/Footer"))


const Body = () => {
    return (
        <>
            <Suspense fallback={Loading}>
                <Navigation />
                <section>
                    <Home />
                    <About />
                    <Resume />
                    <Project />
                    <ContactMe />
                </section>
                <footer>
                    <Footer />
                </footer>
            </Suspense>
        </>
    )
}

export default Body