import React from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import { userInfo, ProjectsProps } from "../../constants/user";

//Style
import "./Project-style.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChevronLeft,
  faChevronRight,
} from "@fortawesome/free-solid-svg-icons";
import { colors } from "../../style";

const ProjectCard = ({
  id,
  projectName,
  projectDomain,
  projectDesc,
  projectLink,
  projectImage,
  status,
}: ProjectsProps | any) => {
  return (
    <div id={id} className="project-card-container">
      <div className="project-img-container">
        <img
          src={
            projectImage ||
            require("../../assets/Images/project_image/common.webp")
          }
          className="project-img"
          alt="logo"
        />
      </div>
      <div className="project-info-container">
        <div style={{ position: "relative" }}>
          <p className="project-title">{projectDomain}</p>
          <p className="project-name">{projectName}</p>
          <p className="project-desc">{projectDesc}</p>
          {status === "PENDING" && (
            <div title="Pending" className="project-status" />
          )}
        </div>
        <br />
        <div>
          <Button
            id="project-card-but"
            aria-label="Check out"
            href={projectLink}
            target="_blank"
          >
            Check Out
          </Button>
        </div>
        <br />
      </div>
    </div>
  );
};

function Project() {
  var containerRef: any = null;

  const projects = userInfo.projects;

  const scrollRight = () => {
    containerRef.scrollBy({ left: -400, behavior: "smooth" });
  };

  const scrollLeft = (e: any) => {
    e.preventDefault();
    containerRef.scrollBy({ left: 400, behavior: "smooth" });
  };

  return (
    <Container
      fluid={true}
      id="Projects"
      className="container project-container"
    >
      <Row>
        <Col lg={12}>
          <br />
          <br />
          <br />
          <br />
          <p className="page-title">Projects</p>
          <p className="project-heading">
            Here is some of cool project that I worked on!
          </p>
          <div className="project-col2">
            <ButtonGroup className="project-toggle-group">
              <Button
                id="project-toggle-but1"
                aria-label="prev"
                onClick={scrollRight}
              >
                <FontAwesomeIcon
                  size="lg"
                  icon={faChevronLeft}
                  color={colors.secondary}
                />
              </Button>
              <Button
                id="project-toggle-but2"
                aria-label="Next"
                onClick={scrollLeft}
              >
                <FontAwesomeIcon
                  size="lg"
                  icon={faChevronRight}
                  color={colors.primary}
                />
              </Button>
            </ButtonGroup>
          </div>
        </Col>
        <Col lg={12}>
          <div
            ref={(node) => (containerRef = node)}
            className="project-card-list-container"
          >
            {projects.map((item, index) => (
              <ProjectCard id={index} {...item} key={index} />
            ))}
          </div>
        </Col>
      </Row>
    </Container>
  );
}

export default React.memo(Project);
