import React, { memo, useState, useEffect } from 'react'
import Container from 'react-bootstrap/Container'
import Row from "react-bootstrap/Row"
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'
import { userInfo } from '../../constants/user'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faInstagram, faFacebook, faGithub, faLinkedinIn } from '@fortawesome/free-brands-svg-icons';
import Typed from 'typed.js'
import "./style.scss"
import { colors } from '../../style'


interface FollowMeContainersProps {
    class?: string
}

const FollowMeContainers = React.memo((props: FollowMeContainersProps) => {
    return (
        <div className={props.class + " socialContainer"}>
            <a href="https://www.instagram.com/akshaydighorikar/" target="__blank">
                <FontAwesomeIcon icon={faInstagram} size="2x" color={colors.secondary} />
            </a>
            <a href="https://www.facebook.com/akshay.dighorikar?ref=bookmarks" target="__blank">
                <FontAwesomeIcon icon={faFacebook} size="2x" color={colors.secondary} />
            </a>
            <a href="https://github.com/Akshay1997a" target="__blank">
                <FontAwesomeIcon icon={faGithub} size="2x" color={colors.secondary} />
            </a>
            <a href="https://www.linkedin.com/in/akshay-dighorikar-0b2583116">
                <FontAwesomeIcon icon={faLinkedinIn} size="2x" color={colors.secondary} />
            </a>
        </div>
    )
})

const ButtonContainer = React.memo(() => {

    const downloadCv = () => {
        window.location.href = userInfo.cvLink
    }

    return (
        <div className="butContainer">
            <Button variant="primary" id="but1" aria-label="Hire" onClick={() => { window.location.href = "#ContactMe" }}>Hire Me</Button>
            <Button variant="primary" id="but2" aria-label="Download CV" onClick={downloadCv}>Get CV</Button>
        </div>
    )
})

function Home() {
    const [width, setWidth] = useState(window.innerWidth)
    var el: any = null;
    var typed: any = null;

    useEffect(() => {
        typed = new Typed(el, {
            strings: userInfo.profession,
            typeSpeed: 50,
            backSpeed: 20,
            backDelay: 2000,
            loop: true,
            showCursor: true,
        })
    }, [])

    window.addEventListener("resize", () => {
        if (width != window.innerWidth) {
            setWidth(window.innerWidth)
        }
    })

    return (
        <Container className="container home-container" id="Home">
            <Row className="row">
                <Col lg={6} className="col1">
                    <Col className="infoContainer">
                        <div className="titleBox">
                            <h1 className="title-first">Hello, I am </h1>
                            {/* <div id="strock" /> */}
                        </div>
                        <h1 className="name">{userInfo.firstName}</h1>
                        <h1 className="name">{userInfo.lastName}</h1>
                        <span className="title" ref={(e) => el = e} style={{ height: 25 }} />
                        <br /><br />
                        <ButtonContainer />
                        <FollowMeContainers class="large-device" />
                    </Col>
                </Col>
                {width > 992 ? (
                    <Col lg={6} className="col2">
                        <img
                            src={require('../../assets/Images/home_img.svg')}
                            className="homeIcon"
                            alt="Home_Img"
                        />
                    </Col>
                ) : (
                        <Col lg={6} className="col3">
                            <Row className="row">
                                <Col lg={12} className="col3">
                                    <div className="avatarContainer">
                                        <img
                                            src={require('../../assets/Images/avatar.webp')}
                                            className="avatar"
                                            alt="avatar"
                                        />
                                    </div>
                                </Col>
                                <Col lg={12} className="col3">
                                    <FollowMeContainers class="small-device" />
                                </Col>
                            </Row>
                        </Col>
                    )}
            </Row>
        </Container>
    )
}

export default memo(() => Home())