export interface EducationProps {
  startYear: string;
  endYear: string;
  cource: string;
  collage: string;
  desc: string;
}

type EndDateType = Date | "Present";

export interface ExperienceProps {
  startDate: Date;
  endDate: EndDateType;
  companyName: string;
  possition: string;
  desc: string;
}

export interface Skills {
  skillName: string;
  experience: number;
}

export interface SkillsProps {
  frontEnd: Array<Skills>;
  backEnd: Array<Skills>;
  mobileAppDev: Array<Skills>;
  other: Array<Skills>;
}

export interface AwardsProps {
  awardName: string;
  issuingOrganization: string;
  dateOfIssue: Date;
  expires?: boolean;
  expirationDate?: Date;
  credentialID: string;
  credentialUrl: string;
}

export interface ResumeProps {
  education?: Array<EducationProps>;
  experience?: Array<ExperienceProps>;
  skills?: SkillsProps;
  awards?: Array<AwardsProps>;
}

export interface ProjectsProps {
  projectName?: string;
  projectDomain?: string;
  projectDesc?: string;
  startDate?: string;
  endDate?: string;
  projectLink?: string;
  projectImage: string | null;
  status: "COMPLETED" | "PENDING";
}

export interface UserInfo {
  fullName: string;
  firstName: string;
  lastName: string;
  profession: Array<string>;
  dob: string;
  addressLine1: string;
  addressLine2: string;
  zipCode: string;
  emailId: string;
  phoneNo: string;
  subDisc: string;
  desc: string;
  cvLink: string;
  projectCompleted: number;
  resume: ResumeProps;
  projects: Array<ProjectsProps>;
}

function age(birthday: Date) {
  birthday = new Date(birthday);
  // eslint-disable-next-line no-new-wrappers
  return new Number(
    (new Date().getTime() - birthday.getTime()) / 31536000000
  ).toFixed(0);
}

