import React, { useState } from 'react'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import Container from 'react-bootstrap/Container'
import "./Navigation-style.scss"

const Bar = () => (
    <div className="icon-bar-container">
        <div className="icon-bar"></div>
        <div className="icon-bar"></div>
        <div className="icon-bar"></div>
    </div>
)

const Navigation = () => {
    const [activeIndex, setActiveIndex] = useState<string | null>("1")

    const addActive = (value: boolean) => {
        if (value) {
            var el = document.getElementsByClassName("navBar").item(0)
            el?.classList.add("active-toggle")
            document.getElementsByClassName("icon-bar").item(0)?.classList.add('active-toggle')
            document.getElementsByClassName("icon-bar").item(1)?.classList.add('active-toggle')
            document.getElementsByClassName("icon-bar").item(2)?.classList.add('active-toggle')
        } else {
            var el = document.getElementsByClassName("navBar").item(0)
            el?.classList.remove("active-toggle")
            document.getElementsByClassName("icon-bar").item(0)?.classList.remove('active-toggle')
            document.getElementsByClassName("icon-bar").item(1)?.classList.remove('active-toggle')
            document.getElementsByClassName("icon-bar").item(2)?.classList.remove('active-toggle')
        }
    }

    window.addEventListener("scroll", (e) => {
        var scroll = window.scrollY
        var offset = 50
        if (scroll > offset) {
            var el = document.getElementsByClassName("navBar").item(0)
            el?.classList.add("active")
        } else {
            var el = document.getElementsByClassName("navBar").item(0)
            el?.classList.remove("active")
        }
    })

    return (
        <Navbar
            expand="lg"
            collapseOnSelect
            fixed="top"
            className="navBar"
            onToggle={addActive}
        >
            <Navbar.Brand>
                <a href="#">
                    <img src={require("../../assets/Icons/Icon.svg")} style={{ width: 60, height: 'auto' }} alt="logo" />
                </a>
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar">
                {/* <img src={require('../../assets/Images/bar.svg')} style={{ width: 30, height: 'auto' }} /> */}
                <Bar />
            </Navbar.Toggle>
            <Navbar.Collapse className=" justify-content-end scrollspy-div">
                <Nav onSelect={(e) => { setActiveIndex(e) }} activeKey={activeIndex} className="justify-content-end">
                    <div className="navigation-bar" />
                    <Nav.Item>
                        <Nav.Link eventKey="1" id="link" href="#Home">Home</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link eventKey="2" id="link" href="#About">About</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link eventKey="3" id="link" href="#Resume" >Resume</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link eventKey="4" id="link" href="#Projects" >Projects</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link eventKey="5" id="link" href="#ContactMe" >Contact Me</Nav.Link>
                    </Nav.Item>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    )
}
export default Navigation